<template>
    <div class="user-index">
        <div class="container">
            <div @click="loginHandel">
                <div class="avatar">
                    <img src="../../assets/user/avatar.png" v-if="!user.avatar">
                    <img :src="user.avatar" v-if="user.avatar">
                </div>
                <div class="nickname">
                    {{ user.nickname ? user.nickname : '请注册/登录' }}
                </div>
            </div>

            <div class="box">
                <div class="residue">
                    <div class="count">{{ user.viewTimes.remaining }}<span style="font-size: 20px">/</span>{{ user.viewTimes.total }}</div>
                    <div class="text">今日剩余观影数</div>
                </div>
                <div class="residue">
                    <div class="count">0</div>
                    <div class="text">推广好友数</div>
                </div>
            </div>
        </div>

        <div class="popularize" @click="$router.push('/user/popularize')">
            <div class="grade">
                <div class="level">
                    <div class="this item">LV{{ grade.this.grade }}.{{ grade.this.title }}</div>
                    <div class="next item">LV{{ grade.next.grade }}.{{ grade.next.title }}</div>
                </div>
                <div class="progress"></div>
                <div class="text">
                    距离升级（增加观影次数）只差<span>{{ grade.progress }}</span>人
                </div>
            </div>
            <div class="button">
                <div class="action">推广升级</div>
            </div>
        </div>

        <div class="menu">
            <div class="item" @click="$router.push('/user/favorite')">
                <div class="ico">
                    <img src="../../assets/user/menu/favorite.png">
                </div>
                <div class="text">我喜欢的</div>
            </div>
            <div class="item" @click="$router.push('/user/message')">
                <div class="ico">
                    <img src="../../assets/user/menu/message.png">
                </div>
                <div class="text">消息通知</div>
            </div>
            <div class="item">
                <div class="ico">
                    <img src="../../assets/user/menu/group.png">
                </div>
                <div class="text">交流群</div>
            </div>
            <div class="item" @click="$router.push('/user/feedback')">
                <div class="ico">
                    <img src="../../assets/user/menu/feedback.png">
                </div>
                <div class="text">意见反馈</div>
            </div>
        </div>

        <div class="navigation">
            <div class="item van-hairline--bottom" @click="$router.push('/user/history')">
                <div class="ico">
                    <img src="../../assets/user/menu/history.png">
                </div>
                <div class="text">历史记录</div>
            </div>
            <div class="item van-hairline--bottom" v-if="false">
                <div class="ico">
                    <img src="../../assets/user/menu/cache.png">
                </div>
                <div class="text">修改密码</div>
            </div>
            <div class="item van-hairline--bottom" @click="loginOutHandel" v-if="user.mode !== 0">
                <div class="ico">
                    <img src="../../assets/user/menu/setting.png">
                </div>
                <div class="text">退出登录</div>
            </div>
        </div>

        <tabbar/>
    </div>
</template>

<script>
    import tabbar from '../../components/tabbar';

    export default {
        components: {
            tabbar
        },
        data() {
            return {
                user: {
                    avatar: null,
                    mode: 0,
                    nickname: null,
                    viewTimes: {
                        total: 0,
                        remaining: 0
                    }
                },
                grade: {
                    this: {
                        title: '英勇黄铜',
                        grade: 0
                    },
                    next: {
                        title: '不屈白银',
                        grade: 0
                    },
                    progress: 0
                }
            }
        },
        activated() {
            this.initUser();
            this.initGrade();
        },
        mounted() {
        },
        methods: {
            initUser() {
                this.$axios.get('/api/app/user/store')
                    .then(response => {
                        let code = response.data.code;
                        let result = response.data.result;
                        if (code === 200) {
                            this.user = result;
                        }
                    })
            },
            initGrade() {
                this.$axios.get('/api/app/user/grade')
                    .then(response => {
                        let code = response.data.code;
                        let result = response.data.result;
                        if (code === 200) {
                            this.grade = result;
                        }
                    })
            },
            loginHandel() {
                if (this.$store.state.platform === 'web') {
                    this.$dialog.confirm({
                        title: '提示',
                        message: '下载APP进行注册登录,免费增加5次观影！',
                        confirmButtonText: '立即下载'
                    }).then(() => {
                        // on confirm
                    }).catch(() => {
                        // on cancel
                    });
                    return;
                }

                if (this.user.mode === 0) {
                    this.$router.push('/login/index');
                }
            },
            loginOutHandel() {
                if (this.user.mode === 0) {
                    return;
                }

                this.$dialog.confirm({
                    title: '提示',
                    message: '确认退出登录？'
                }).then(() => {
                    localStorage.clear();
                    this.user = {
                        avatar: null,
                        grade: 0,
                        nickname: null,
                        residue: 0
                    };
                    this.$toast.loading({
                        duration: 0,
                        forbidClick: true,
                        mask: false,
                        message: '退出中...'
                    });
                    this.$router.push('/login/index');

                })
            }
        }
    }
</script>

